<template>
  <div id="distribute">
    <div class="statics">
      <div class="statics-item">
        <img src="../../../assets/images/icon_jigoushuliang.png" style="width: 76px;height:76px;margin-right: 10px;">
        <div class="statics-item-data">
          <span>机构数量</span>
          <div><span class="statics-num-1">
              <CountTo :startVal='startVal' :endVal='citiesNumData.num1' :duration='duration' separator="," />
            </span>个</div>
        </div>
      </div>
      <div class="statics-item">
        <img src="../../../assets/images/icon_xiangmushuliang.png" style="width: 76px;height:76px;margin-right: 10px;">
        <div class="statics-item-data">
          <span>项目数量(进行中)</span>
          <div><span class="statics-num-2">
              <CountTo :startVal='startVal' :endVal='projectNum' :duration='duration' separator="," />
            </span>个</div>
        </div>
      </div>
      <div class="statics-item">
        <img src="../../../assets/images/icon_shenfen.png" style="width: 76px;height:76px;margin-right: 10px;">
        <div class="statics-item-data">
          <span>省市</span>
          <div><span class="statics-num-3">
              <CountTo :startVal='startVal' :endVal='citiesNumData.num2' :duration='duration' separator="," />
            </span>个</div>
        </div>
      </div>
    </div>
    <div id="map">
      <china-map :cdata="mapDistributeData" :distributeType="radioType" />
    </div>
    <div class="btn-tools">
      <div :class="!radioType ? 'btn-1 active' : 'btn-1'" @click="toggleDistribute(0)">机构分布</div>
      <div :class="radioType ? 'btn-1 active' : 'btn-1'" @click="toggleDistribute(1)">项目分布</div>
    </div>

  </div>
</template>

<script>
import { getCitiesNumApi, ProjectDistributeApi } from "@/api/home.js";

import CountTo from "vue-count-to";
import ChinaMap from "@/components/ChinaMap";

export default {
  props: {
    projectNum: {
      type: [String, Number],
      require: true,
      default: 0,
    },
    // ApiData: {
    //   type: Array,
    //   require: true,
    //   default: () => { }
    // }
  },
  data() {
    return {
      radioType: 0,
      // cdata: [
      //   { name: "湖南", value: 9999 },
      //   { name: "广西", value: 277 },
      //   { name: "云南", value: 433 },
      //   { name: "海南", value: 999 },
      //   { name: "四川", value: 22 },
      // ],
      startVal: 0,
      duration: 2000,
      citiesNumData:[],
      mapDistributeData: [
        {
          name: "湖南",
          value: 0,
        },
        {
          name: "海南",
          value: 0,
        },
        {
          name: "江西",
          value: 0,
        },
        {
          name: "青海",
          value: 0,
        },
        {
          name: "重庆",
          value: 0,
        },
        {
          name: "广东",
          value: 0,
        },
        {
          name: "四川",
          value: 0,
        },
        {
          name: "贵州",
          value: 0,
        },
        {
          name: "北京",
          value: 0,
        },
        {
          name: "云南",
          value: 0,
        },
        {
          name: "广西",
          value: 0,
        },
        {
          name: "新疆",
          value: 0,
        },
        {
          name: "雄安",
          value: 0,
        },
        {
          name: "浙江",
          value: 0,
        },
        {
          name: "安徽",
          value: 0,
        },
        {
          name: "西藏",
          value: 0,
        },
        {
          name: "福建",
          value: 0,
        },
        {
          name: "甘肃",
          value: 0,
        },
        {
          name: "内蒙古",
          value: 0,
        },
        {
          name: "陕西",
          value: 0,
        },
        {
          name: "青岛",
          value: 0,
        },
        {
          name: "宁夏",
          value: 0,
        },
      ],
    };
  },

  components: {
    ChinaMap,
    CountTo,
  },
  mounted() {
    this.getData();
  },
  methods: {
    //切换分布类型
    toggleDistribute(val) {
      this.radioType = val;
    },
    getData() {
      getCitiesNumApi().then((res) => {
        const { code, data } = res;
        if (code == 0) {
          this.citiesNumData = data;
        }
      });
      ProjectDistributeApi().then((res) => {
        const { code, data } = res;
        if (code == 0) {
          data.forEach((item) => {
            if (
              item.name == "湖南总部" ||
              item.name == "郴州分公司" ||
              item.name == "永州分公司" ||
              item.name == "衡阳分公司" ||
              item.name == "邵阳分公司"
            ) {
              this.mapDistributeData[0].value =
                this.mapDistributeData[0].value + item.value;
            } else if (
              item.name == "海南分公司" ||
              item.name == "中大（海南）智能科技" ||
              item.name == "海南片区项目部"
            ) {
              this.mapDistributeData[1].value =
                this.mapDistributeData[1].value + item.value;
            } else if (item.name == "江西分公司" || item.name == "上饶办事处") {
              this.mapDistributeData[2].value =
                this.mapDistributeData[2].value + item.value;
            } else if (
              item.name == "青海分公司" ||
              item.name == "西北片区项目部"
            ) {
              this.mapDistributeData[3].value =
                item.value + this.mapDistributeData[3].value;
            } else if (
              item.name == "重庆分公司" ||
              item.name == "华中片区项目部"
            ) {
              this.mapDistributeData[4].value =
                item.value + this.mapDistributeData[4].value;
            } else if (
              item.name == "广东分公司" ||
              item.name == "深圳分公司" ||
              item.name == "华南片区项目部"
            ) {
              this.mapDistributeData[5].value =
                item.value + this.mapDistributeData[5].value;
            } else if (item.name == "四川分公司") {
              this.mapDistributeData[6].value = item.value;
            } else if (item.name == "贵州分公司") {
              this.mapDistributeData[7].value = item.value;
            } else if (
              item.name == "北京分公司" ||
              item.name == "华北片区项目部" ||
              item.name == "雄安分公司"
            ) {
              this.mapDistributeData[8].value =
                item.value + this.mapDistributeData[8].value;
            } else if (
              item.name == "云南分公司" ||
              item.name == "安宁分公司" ||
              item.name == "西南片区项目部"
            ) {
              this.mapDistributeData[9].value =
                item.value + this.mapDistributeData[9].value;
            } else if (item.name == "广西分公司") {
              this.mapDistributeData[10].value =
                item.value + this.mapDistributeData[10].value;
            } else if (
              item.name == "新疆分公司" ||
              item.name == "喀什分公司" ||
              item.name == "哈密分公司"
            ) {
              this.mapDistributeData[11].value =
                item.value + this.mapDistributeData[11].value;
            } else if (item.name == "雄安分公司") {
              this.mapDistributeData[12].value = 0;
            } else if (
              item.name == "浙江办事处" ||
              item.name == "华东片区项目部"
            ) {
              this.mapDistributeData[13].value =
                item.value + this.mapDistributeData[13].value;
            } else if (item.name == "安徽办事处") {
              this.mapDistributeData[14].value = item.value;
            } else if (item.name == "西藏分公司") {
              this.mapDistributeData[15].value = item.value;
            } else if (item.name == "福建办事处") {
              this.mapDistributeData[16].value = item.value;
            } else if (item.name == "甘肃办事处") {
              this.mapDistributeData[17].value = item.value;
            } else if (item.name == "内蒙古办事处") {
              this.mapDistributeData[18].value = item.value;
            } else if (item.name == "陕西办事处") {
              this.mapDistributeData[19].value = item.value;
            } else if (item.name == "青岛分公司") {
              this.mapDistributeData[20].value = item.value;
            } else if (item.name == "宁夏办事处") {
              this.mapDistributeData[21].value = item.value;
            }
          });
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#distribute {
  width: 887px;
  height: 910px;
  position: relative;

  .statics {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;

    .statics-item {
      display: flex;
      // align-items: center;

      .statics-item-data {
        color: #e7f0f5;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .statics-num-1 {
          color: #50c6ff;
          font-size: 36px;
          margin-right: 5px;
        }

        .statics-num-2 {
          color: #2be2fb;
          font-size: 36px;
          margin-right: 5px;
        }

        .statics-num-3 {
          color: #ffad41;
          font-size: 36px;
          margin-right: 5px;
        }
      }
    }
  }

  .btn-tools {
    position: absolute;
    bottom: 90px;

    .btn-1 {
      width: 153px;
      height: 53px;
      line-height: 53px;
      text-align: center;
      background: url("~@/assets/images/button_bg.png") no-repeat;
      margin-bottom: 16px;

      &:hover {
        cursor: pointer;
        // background: url("~@/assets/images/button_bg_1.png") no-repeat;
      }
    }

    .btn-1.active {
      background: url("~@/assets/images/button_bg_1.png") no-repeat;
    }
  }
}
</style>
