<template>
  <div id="realtime-overview">
    <img src="../../../assets/images/title_ssgl.png" />

    <div class="bussiness-info">
      <div class="bussiness-item" v-for="(item, index) in realTimeData" :key="index" @mouseover="handleMouseOver(index)"
        @mouseleave="handleMouseLeave(index)" @click="handleClick(index)">
        <img :src="!item.flag ? item.img : item.img_hover" class="bussiness-img">
        <div class="bussiness-data">
          <span>{{ item.name }}</span>
          <div style="display: flex;">
            <span class="bussiness-num">
              <CountTo :startVal='startVal' :endVal='item.count' :duration='duration' separator="," />
            </span>{{ item.unit }}
          </div>
        </div>
      </div>
    </div>
    <el-drawer title="详情" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 0">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber" />
          <el-table-column label="报告状态" prop="reportStatus" width="80" />
          <el-table-column label="检测指标" prop="parameterName" />
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="委托时间" prop="entrustDate" width="110" />
          <el-table-column label="收样时间" prop="sampleReceiveTime" width="110" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 1">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <el-table-column label="样品编号" prop="sampleNumber">
          </el-table-column>
          <el-table-column label="收样时间" prop="sampleReceiveTime" />
          <el-table-column label="收样人员" prop="receiverName" />
          <el-table-column label="主检部门" prop="mainDept" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;"
          v-if="index == 2 || index == 3">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="样品编号" prop="sampleNumber">
          </el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <!-- <el-table-column label="任务下发时间" prop="receiveTime" /> -->
          <el-table-column label="检测参数" prop="parameterName" />
          <el-table-column label="分配状态" prop="taskStatus" />
          <el-table-column label="执行部门" prop="mainDept" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background
            layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
            @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>

  </div>
</template>

<script>
import { ReportListApi } from "@/api/home.js"
import CountTo from "vue-count-to"
import ycjbg from "../../../assets/images/icon_jinrichujubaogao.png"
import ycjbg_hover from "../../../assets/images/icon_yichujubaogao_1.png"

import dcjbg from "../../../assets/images/icon_shouyang.png"
import dcjbg_hover from "../../../assets/images/icon_jinrishouyang_1.png"

import sbzl from "../../../assets/images/icon_jinrijiancerenwu.png"
import sbzl_hover from "../../../assets/images/icon_jinrijiance_1.png"

import zzcs from "../../../assets/images/icon_dangqianjiancerenwu.png"
import zzcs_hover from "../../../assets/images/icon_dangqianzaijian_1.png"

export default {

  props: {
    ApiData: {
      type: Object,
      require: true,
      default: () => { }
    }
  },
  components: {
    CountTo
  },
  data () {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    }
    return {
      startVal: 0,
      duration: 1000,
      realTimeData: [
        { name: "今日出具报告", count: 0, img: ycjbg, unit: "份", img_hover: ycjbg_hover, flag: 0 },
        { name: "今日收样量", count: 0, img: dcjbg, unit: "份", img_hover: dcjbg_hover, flag: 0 },
        { name: "今日检测任务", count: 0, img: sbzl, unit: "个", img_hover: sbzl_hover, flag: 0 },
        { name: "当前在检任务", count: 0, img: zzcs, unit: "个", img_hover: zzcs_hover, flag: 0 },
      ],
      query: Object.assign({}, this.initQuery),
      drawerVisible: false,
      tableValue: [],
      index: 0,
      Loading: false
    }
  },
  watch: {
    ApiData: {
      handler (val) {
        if (val) {
          this.$nextTick(() => {
            this.realTimeData[0].count = this.ApiData.outReportAmount
            this.realTimeData[1].count = this.ApiData.addSampleAmount
            this.realTimeData[2].count = this.ApiData.addTaskAmount
            this.realTimeData[3].count = this.ApiData.testingTaskAmount
          })
        }
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    handleMouseOver (index) {
      this.realTimeData[index].flag = 1
    },
    handleMouseLeave (index) {
      this.realTimeData[index].flag = 0
    },
    handleClick (index) {
      this.index = index
      this.drawerVisible = true
      this.toQuery()
    },
    toQuery () {
      this.Loading = true
      if (this.index == 0) {
        this.query = { ...this.query, queryType: '今日出具报告' }
      }
      if (this.index == 1) {
        this.query = { ...this.query, queryType: '今日收样量' }
      }
      if (this.index == 2) {
        this.query = { ...this.query, queryType: '今日检测任务' }
      }
      if (this.index == 3) {
        this.query = { ...this.query, queryType: '当前在检任务' }
      }
      ReportListApi(this.query).then(res => {
        if (res.code == 0) {
          this.Loading = false
          this.tableValue = res.data.records
          this.query.total = res.data.total
        }
      }).finally(() => {
        this.Loading = false
      })
    },
    paginationCurrentChange (pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex }
      this.toQuery()
    },
    handleSizeChange (pageSize) {
      this.query = { ...this.query, pageSize: pageSize }
      this.toQuery()
    },
  }
}
</script>

<style lang="scss" scoped>
#realtime-overview {
  width: 460px;
  height: 290px;
  // background: url("~@/assets/images/title_ssgl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;


  .bussiness-info {
    padding: 18px 15px 0 15px;

    display: grid;
    grid-template-columns: repeat(2, 50%);
    // grid-template-rows: repeat(3, 33.33%);
    grid-row-gap: 36px;
    grid-column-gap: 20px;


    .bussiness-item {
      color: #DADFE4;
      display: flex;
      font-size: 14px;

      .bussiness-img {
        width: 88px;
        height: 60px;
        margin-right: 15px;
      }

      .bussiness-data {
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .bussiness-num {
          color: #96CEFF;
          font-size: 24px;
          margin-right: 5px;
        }
      }

    }
  }

}
</style>
