<template>
  <div>
    <Echart id="ChinaMapChart" ref="ChinaMapChartRef" :options="options" height="692px" width="887px"></Echart>
    <el-drawer :title="drawerTitle" :visible.sync="drawerVisible" size="1050px" append-to-body modal-append-to-body @close="closeDrawer">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <div class="radio-tools">
          <div :class="radioType == 1 ? 'radio-item active' : 'radio-item'" @click="toggleType(1)">
            月
          </div>
          <div :class="radioType == 2 ? 'radio-item active' : 'radio-item'" @click="toggleType(2)">
            季度
          </div>
          <div :class="radioType == 3 ? 'radio-item active' : 'radio-item'" @click="toggleType(3)">
            年
          </div>
        </div>
        <div class="statics">
          <div class="statics-item" @click="handleClick(1)">
            <div class="statics-item-data">
              <span>出具报告</span>
              <div style="margin-top:10px;">
                <span class="statics-num-3">
                  <CountTo :startVal='startVal' :endVal='InfoData.outReportAmount' :duration='duration' separator="," />
                </span>份
              </div>
            </div>
          </div>
          <div class="statics-item" @click="handleClick(2)">
            <div class="statics-item-data">
              <span>在检任务</span>
              <div style="margin-top:10px;"><span class="statics-num-3">
                  <CountTo :startVal='startVal' :endVal='InfoData.testingTaskAmount' :duration='duration' separator="," />
                </span>个</div>
            </div>
          </div>
          <div class="statics-item" @click="handleClick(3)">
            <div class="statics-item-data">
              <span>进行中项目</span>
              <div style="margin-top:10px;"><span class="statics-num-3">
                  <CountTo :startVal='startVal' :endVal='InfoData.projectAmount' :duration='duration' separator="," />
                </span>个</div>
            </div>
          </div>
          <div class="statics-item" @click="handleClick(4)">
            <div class="statics-item-data">
              <span>检测人员</span>
              <div style="margin-top:10px;"><span class="statics-num-3">
                  <CountTo :startVal='startVal' :endVal='InfoData.inspectorAmount' :duration='duration' separator="," />
                </span>个</div>
            </div>
          </div>
        </div>
        <div class="drawer-subtitle">实验室监控</div>
        <div style="width:95%;margin: 0 auto;">
          <el-divider></el-divider>
        </div>
        <div class="video-container" v-if="drawerVisible">
          <div class="video-item" v-for="(item, index) in videoList" :key="index">
            <jessibuca-demo :equNumber="item.number" />
            <p style="color:#FFFFFF;line-height: 50px;text-align: center;">{{ item.name }}</p>
          </div>
          <!-- <VideoComponent /> -->
        </div>
      </div>
    </el-drawer>

    <el-drawer title="数据详情" :visible.sync="drawerVisible2" size="1050px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading2">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index2 == 1">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber" />
          <el-table-column label="报告状态" prop="reportStatus" width="80" />
          <el-table-column label="检测指标" prop="parameterName" />
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="委托时间" prop="entrustDate" width="110" />
          <el-table-column label="收样时间" prop="sampleReceiveTime" width="110" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index2 == 2">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <el-table-column label="样品编号" prop="sampleNumber">
          </el-table-column>
          <el-table-column label="收样时间" prop="sampleReceiveTime" />
          <el-table-column label="检测参数" prop="parameterName" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index2 == 3">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="项目编号" prop="projectNumber" />
          <el-table-column label="合同编号" prop="contractNumber" />
          <el-table-column label="所属公司" prop="ownedCompany" width="100" />
          <el-table-column label="所属片区" prop="area" width="100" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index2 == 4">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="用户名称" prop="userName" width="100" />
          <el-table-column label="员工编号" prop="userNumber" width="100" />
          <el-table-column label="性别" prop="userSex" width="80">
            <template slot-scope="scope">
              {{ scope.row.userSex ? "男" : "女" }}
            </template>
          </el-table-column>
          <el-table-column label="所属部门" prop="deptNames" show-overflow-tooltip />
          <el-table-column label="手机号" prop="userPhone" />
          <el-table-column label="岗位" prop="positions" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
            @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import Echart from "@/common/echart";
import CountTo from "vue-count-to";
import JessibucaDemo from "../JessibucaDemo.vue";
// import VideoComponent from '../VideoComponent.vue'

import {
  queryCompanyInfoApi,
  queryCompanyInfoListApi,
  queryCompanyProjectListApi,
  queryCompanyUserListApi,
} from "@/api/home.js";

export default {
  data() {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    };
    return {
      options: {},
      seriesData: [],
      index: 0,
      position: [
        [110.982279, 28.19409, 0],
        [109.33119, 19.031971, 0],
        [115.892151, 28.676493, 0],
        [97.778916, 36.623178, 0],
        [106.504962, 29.533155, 0],
        [113.280637, 23.125178, 0],
        [104.065735, 30.659462, 0],
        [106.713478, 26.578343, 0],
        [116.405285, 39.904989, 0],
        [102.712251, 25.040609, 0],
        [108.320004, 22.82402, 0],
        [87.617733, 43.792818, 0],
        [116.117106, 39.164298, 0],
        [120.153576, 30.287459, 0],
        [117.283042, 31.86119, 0],
        [91.132212, 29.660361, 0],
        [119.306239, 26.075302, 0],
        [103.823557, 36.058039, 0],
        [111.670801, 40.818311, 0],
        [108.948024, 34.263161, 0],
        [120.38, 36.07, 0],
        [106.278179, 38.46637, 0],
      ],
      drawerVisible: false,
      drawerTitle: "",
      radioType: 1,
      Loading: false,
      startVal: 0,
      duration: 2000,
      endVal1: 24,
      endVal2: 30,
      InfoData: {
        addSampleAmount: 0,
        addTaskAmount: 0,
        outReportAmount: 0,
        testingTaskAmount: 0,
      },

      jessibuca: null,
      version: "",
      playing: false,
      quieting: true,
      loaded: false, // mute
      showOperateBtns: false,
      showBandwidth: false,
      err: "",
      performance: "",
      useWCS: false,
      useMSE: true,
      useOffscreen: false,
      drawerVisible2: false,
      index2: 1,
      query: Object.assign({}, this.initQuery),
      Loading2: false,
      tableValue: [],
      videoList: [],
    };
  },
  components: {
    Echart,
    CountTo,
    JessibucaDemo,
  },
  props: {
    cdata: {
      type: Array,
      default: () => [],
    },
    distributeType: {
      type: Number,
      default: 0,
    },
  },
  watch: {
    distributeType: {
      handler(val) {
        if (!val) {
          this.initOriganChart();
          this.timeFun();
          this.$nextTick(() => {
            //添加悬浮事件
            const myChart = this.$echarts.init(
              document.getElementById("ChinaMapChart")
            );
            myChart.on("mouseover", (params) => {
              if (params.componentSubType == "effectScatter") {
                this.handleMouseOver(params.dataIndex);
              }
            });
            myChart.on("mouseout", (params) => {
              //鼠标移出
              if (params.componentSubType == "effectScatter") {
                // 开启定时器
                this.index = 0;
                this.timeFun();
              }
            });
            myChart.on("click", (params) => {
              if (params.componentSubType == "effectScatter") {
                this.openDrawer(params);
              }
            });
          });
        } else {
          this.initProjectChart();
        }
      },
      immediate: true,
      deep: true,
    },
  },
  beforeDestroy() {
    clearInterval(this.timing);
  },
  mounted() {
    this.$once("hook:beforeDestroy", () => {
      clearInterval(this.timing);
      this.timing = null;
    });
  },

  methods: {
    handleClick(index) {
      this.index2 = index;
      this.drawerVisible2 = true;
      this.toQueryDetail();
    },
    toQueryDetail() {
      var queryApi = "";
      if (this.index2 == 1) {
        this.query = { ...this.query, queryType: "出具报告" };
        queryApi = queryCompanyInfoListApi;
      }
      if (this.index2 == 2) {
        this.query = { ...this.query, queryType: "在检任务" };
        queryApi = queryCompanyInfoListApi;
      }
      if (this.index2 == 3) {
        this.query = { ...this.query };
        queryApi = queryCompanyProjectListApi;
      }
      if (this.index2 == 4) {
        this.query = { ...this.query };
        queryApi = queryCompanyUserListApi;
      }
      this.Loading2 = true;
      queryApi({
        ...this.query,
        dateType: this.radioType,
        companyName: this.drawerTitle,
      })
        .then((res) => {
          if (res.code == 0) {
            this.tableValue = res.data.records;
            this.query.total = res.data.total;
            this.Loading2 = false;
          }
        })
        .finally(() => {
          this.Loading2 = false;
        });
    },
    paginationCurrentChange(pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex };
      this.toQueryDetail();
    },
    handleSizeChange(pageSize) {
      this.query = { ...this.query, pageSize: pageSize };
      this.toQueryDetail();
    },

    toggleType(type) {
      this.radioType = type;
      this.toQuery();
    },
    openDrawer(params) {
      this.drawerVisible = true;
      this.drawerTitle = params.name;
      this.toQuery();
      this.playVideo();
      // this.create()
      // this.play()
    },
    playVideo() {
      if (this.drawerTitle == "湖南总部") {
        this.videoList = [
          {
            number: "34020000001180000003/34020000001320000026",
            name: "力学室内硬度实验室",
          },
          {
            number: "34020000001180000003/34020000001320000027",
            name: "力学室内拉伸实验室",
          },
          // // { number: "34020000001310000011/34020000001310000031", name: "13楼试剂室" },
          {
            number: "34020000001180000001/34020000001310000031",
            name: "15楼ICP-MS室",
          },
          // { number: "34020000001180000001/34020000001310000026", name: "15楼试剂室" },
        ];
      } else if (this.drawerTitle == "海南分公司") {
        this.videoList = [
          {
            number: "34020000001180000012/34020000001320000002",
            name: "土工合成材料防水密封胶室",
          },
          {
            number: "34020000001180000012/34020000001320000003",
            name: "化学室",
          },
          {
            number: "34020000001180000012/34020000001320000005",
            name: "室内环境室",
          },
          // { number: "34020000001180000012/34020000001320000006", name: "电线电缆" },
          // { number: "34020000001180000012/34020000001320000007", name: "力学室" },
          // { number: "34020000001180000012/34020000001320000008", name: "集料室" },
          // { number: "34020000001180000012/34020000001320000010", name: "现场设备室 2 楼" },
          // { number: "34020000001180000012/34020000001320000023", name: "燃烧室" },
          // { number: "34020000001180000012/34020000001320000022", name: "门窗保温" },
          // { number: "34020000001180000012/34020000001320000020", name: "沥清原材室" },
        ];
      }
    },
    closeDrawer() {
      this.InfoData = {
        addSampleAmount: 0,
        addTaskAmount: 0,
        outReportAmount: 0,
        testingTaskAmount: 0,
      };
      this.videoList = [];
      // this.jessibuca.destroy()
    },
    toQuery() {
      this.Loading = true;
      queryCompanyInfoApi({
        companyName: this.drawerTitle,
        dateType: this.radioType,
      })
        .then((res) => {
          // console.log(res)
          if (res.code == 0) {
            this.Loading = false;
            this.InfoData = res.data;
          }
        })
        .finally(() => {
          this.Loading = false;
        });
    },

    initOriganChart() {
      const geoCoordMap = {
        湖南总部: [112.982279, 28.19409, 999],
        广东分公司: [112.280637, 23.125178, 80],
        // 深圳分公司: [113.624642, 22.443092, 80],
        江西分公司: [115.892151, 28.676493, 80],
        浙江分公司: [120.153576, 30.287459, 80],
        安徽分公司: [117.283042, 31.86119, 80],
        // 江苏分公司: [118.767413, 32.041544, 80],
        // 河南分公司: [113.665412, 34.757975, 80],
        新疆分公司: [87.617733, 43.792818, 80],
        西藏分公司: [91.132212, 29.660361, 80],
        // 山东分公司: [117.000923, 36.675807, 90],
        北京分公司: [116.405285, 39.904989, 80],
        雄安新区分公司: [116.208793, 38.901552, 80],
        甘肃分公司: [103.823557, 35.058039, 80],
        青海分公司: [95.043533, 35.726403, 12],
        重庆分公司: [106.504962, 29.533155, 99],
        四川分公司: [104.065735, 30.659462, 65],
        贵州分公司: [106.713478, 26.578343, 55],
        云南分公司: [102.712251, 25.040609, 77],
        安宁分公司: [102.4047, 23.854993, 88],
        广西分公司: [108.320004, 22.82402, 99],
        // 珠海分公司: [113.552724, 22.255899, 66],
        海南分公司: [109.754859, 19.189767, 120],
      };
      this.seriesData = [
        {
          name: "湖南总部",
        },
        {
          name: "广东分公司",
        },
        // {
        //   name: '深圳分公司',
        // },
        {
          name: "江西分公司",
        },
        {
          name: "浙江分公司",
        },
        {
          name: "安徽分公司",
        },
        // {
        //   name: '江苏分公司',
        // },
        // {
        //   name: '河南分公司',
        // },
        // {
        //   name: '山东分公司',
        // },
        {
          name: "雄安新区分公司",
        },
        {
          name: "北京分公司",
        },
        {
          name: "新疆分公司",
        },
        {
          name: "西藏分公司",
        },
        {
          name: "甘肃分公司",
        },
        {
          name: "青海分公司",
        },
        {
          name: "重庆分公司",
        },
        {
          name: "四川分公司",
        },
        {
          name: "贵州分公司",
        },
        {
          name: "云南分公司",
        },
        {
          name: "安宁分公司",
        },
        {
          name: "广西分公司",
        },
        // {
        //   name: '珠海分公司',
        // },
        {
          name: "海南分公司",
        },
      ];
      let convertData = function (data) {
        let scatterData = [];
        for (var i = 0; i < data.length; i++) {
          var geoCoord = geoCoordMap[data[i].name];
          if (geoCoord) {
            scatterData.push({
              name: data[i].name,
              value: geoCoord.concat(data[i].value),
            });
          }
        }
        return scatterData;
      };
      this.options = {
        showLegendSymbol: true,
        tooltip: {
          show: false,
          trigger: "item",
          textStyle: {
            fontSize: 14,
            lineHeight: 22,
          },
          position: (point) => {
            // 固定在顶部
            return [point[0] + 50, point[1] - 20];
          },
        },
        visualMap: {
          min: 0,
          max: 10,
          show: false,
          seriesIndex: 0,
          // 颜色
          inRange: {
            color: ["rgba(41,166,206, .5)", "rgba(69,117,245, .9)"],
          },
        },
        // 底部背景
        geo: {
          show: true,
          aspectScale: 0.78, //长宽比
          zoom: 1.2,
          top: "10%",
          // left: '16%',
          map: "china",
          roam: false,
          itemStyle: {
            normal: {
              areaColor: "#00D2FF",
              shadowColor: "#00D2FF",
              shadowBlur: 5,
              shadowOffsetX: 0,
              shadowOffsetY: 10,
            },
            emphasis: {
              areaColor: "#00aeef",
            },
          },
        },
        series: [
          {
            name: "相关指数",
            type: "map",
            aspectScale: 0.78, //长宽比
            zoom: 1.2,
            mapType: "china", // 自定义扩展图表类型
            top: "10%",
            // left: '16%',
            itemStyle: {
              normal: {
                color: "red",
                areaColor: "#001A72",
                borderColor: "#AFDDFF",
                borderWidth: 1,
                shadowBlur: 0,
                shadowColor: "#AFDDFF",
              },
              emphasis: {
                color: "red",
                areaColor: "#001A72",
                borderColor: "#AFDDFF",
                borderWidth: 1,
                shadowBlur: 0,
                shadowColor: "#AFDDFF",
              },
            },
            label: {
              formatter: (params) => `${params.name}`,
              show: true,
              position: "insideRight",
              textStyle: {
                fontSize: 14,
                color: "#F5F5F5",
              },
              emphasis: {
                textStyle: {
                  color: "#F5F5F5",
                },
              },
            },

            data: [],
          },
          {
            tooltip: {
              show: true,
              position: "top",
              backgroundColor: "rgba(0,67,161,0.91)",
              borderColor: "#AFDDFF",
              borderWidth: "2",
              extraCssText: "width:125px;height:58px;",
              formatter: (params) => {
                var str = `<div style="height: 58px;width: 125px;text-align:center;line-height:40px;">
                    ${params.name}</div>`;
                return str;
              },
            },
            // name: '前5',
            type: "effectScatter",
            coordinateSystem: "geo",
            data: convertData(this.seriesData),
            symbolSize: 12,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
            },
            hoverAnimation: true,
            label: {
              normal: {
                formatter: "{b}",
                position: "right",
                show: false,
              },
            },
            itemStyle: {
              normal: {
                color: "#73E2FF",
                shadowBlur: 10,
                shadowColor: "#333",
              },
              emphasis: {
                color: "#FFAF00",
                shadowBlur: 10,
                shadowColor: "#333",
              },
            },
            zlevel: 1,
          },
        ],
      };
      // 重新选择区域
    },

    timeFun() {
      this.timing = setInterval(() => {
        var myChart = this.$echarts.init(
          document.getElementById("ChinaMapChart")
        );
        this.clearAllTip();

        myChart.dispatchAction({
          type: "showTip",
          seriesIndex: 1,
          dataIndex: this.index,
        });
        myChart.dispatchAction({
          type: "highlight",
          seriesIndex: 1,
          dataIndex: this.index,
        });

        this.index =
          this.index == this.seriesData.length - 1 ? 0 : this.index + 1;
      }, 6000);
    },

    clearAllTip() {
      var myChart = this.$echarts.init(
        document.getElementById("ChinaMapChart")
      );
      for (var i = 0; i < this.seriesData.length; i++) {
        myChart.dispatchAction({
          type: "downplay",
          seriesIndex: 1,
          dataIndex: i,
        });
      }
    },

    handleMouseOver(index) {
      clearInterval(this.timing); //悬浮时清除定时器

      this.clearAllTip();
      var myChart = this.$echarts.init(
        document.getElementById("ChinaMapChart")
      );
      myChart.dispatchAction({
        type: "showTip",
        seriesIndex: 1,
        dataIndex: index,
      });
      myChart.dispatchAction({
        type: "highlight",
        seriesIndex: 1,
        dataIndex: index,
      });
    },

    initProjectChart() {
      this.options = {
        showLegendSymbol: true,
        tooltip: {
          show: false,
          trigger: "item",
          textStyle: {
            fontSize: 14,
            lineHeight: 22,
          },
          position: (point) => {
            // 固定在顶部
            return [point[0] + 50, point[1] - 20];
          },
        },
        visualMap: {
          min: 0,
          max: 1000,
          left: 20,
          bottom: 120,
          show: true,
          textStyle: {
            color: "#fff",
          },
          inRange: {
            color: ["#012BB8", "#003AFF", "#1182FF"],
          },
          pieces: [
            {
              gt: 1000,
              label: "1000以上",
              color: "#1182FF",
            },
            {
              gte: 100,
              lte: 1000,
              label: "100 - 500 ",
              color: "#003AFF",
            },
            {
              gte: 1,
              lt: 100,
              label: "1 - 100 ",
              color: "#012BB8",
            },
            {
              value: 0,
              label: "无",
              color: "#001A72",
            },
          ],
        },

        // 底部背景
        geo: {
          show: true,
          aspectScale: 0.78, //长宽比
          zoom: 1.2,
          top: "10%",
          // left: '16%',
          map: "china",
          roam: false,
          itemStyle: {
            normal: {
              areaColor: "#00D2FF",
              shadowColor: "#00D2FF",
              shadowBlur: 5,
              shadowOffsetX: 0,
              shadowOffsetY: 10,
            },
            emphasis: {
              areaColor: "#00aeef",
            },
          },
        },
        series: [
          {
            name: "项目分布",
            type: "map",
            aspectScale: 0.78, //长宽比
            zoom: 1.2,
            mapType: "china", // 自定义扩展图表类型
            top: "10%",
            // left: '16%',
            tooltip: {
              show: true,
              position: "top",
              backgroundColor: "rgba(0,67,161,0.91)",
              borderColor: "#AFDDFF",
              borderWidth: "2",
              extraCssText: "width:125px;height:58px;",
              formatter: (params) => {
                // console.log(params)
                for (var i = 0; i < this.cdata.length; i++) {
                  if (params.name == this.cdata[i].name) {
                    var str = `<div style="height: 58px;width: 125px;text-align:center;line-height:40px;">
                    项目数量: ${this.cdata[i].value}</div>`;
                    return str;
                  }
                }
              },
            },
            itemStyle: {
              normal: {
                // color: 'red',
                areaColor: "#001A72",
                borderColor: "#AFDDFF",
                borderWidth: 1,
                shadowBlur: 0,
                shadowColor: "#AFDDFF",
              },
              emphasis: {
                // color: 'red',
                areaColor: "#00E1FF",
                borderColor: "#AFDDFF",
                borderWidth: 1,
                shadowBlur: 0,
                shadowColor: "#AFDDFF",
              },
            },
            label: {
              formatter: (params) => `${params.name}`,
              show: true,
              position: "insideRight",
              textStyle: {
                fontSize: 14,
                color: "#F5F5F5",
              },
              emphasis: {
                textStyle: {
                  color: "#F5F5F5",
                },
              },
            },
            data: this.cdata,
            zlevel: 1,
          },
        ],
      };

      var myChart = this.$echarts.init(
        document.getElementById("ChinaMapChart")
      );

      myChart.dispatchAction({
        type: "showTip",
        seriesIndex: 1,
        dataIndex: 0,
      });
      myChart.dispatchAction({
        type: "highlight",
        seriesIndex: 1,
        dataIndex: 0,
      });
    },
  },
};
</script>
