<template>
  <div id="index" ref="appRef">
    <div class="bg">
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div v-else class="host-body">
        <Header headerType="home" />
        <div class="echart-body">
          <div class="body-left">
            <bussiness-overview class="body-left-top" :ApiData="bussinessOverviewData" />
            <project-statics class="body-left-bottom" :ApiData="projectStatisticsData"
              :ProjectReportData="projectReportData" />
          </div>
          <div class="body-center">
            <Distribute :projectNum="projectNum" />
          </div>
          <div class="body-right">
            <realtime-overview class="body-right-top" :ApiData="realtimeOverviewData" />
            <report-statics class="body-right-bottom" :ApiData="deptReportData" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  BussinessOverviewApi, RealTimeOverviewApi,
  ProjectStatisticsApi, ReportStatisticsApi, ProjectDistributeApi, BranchProjectReportApi, ProjectReportApi
} from "@/api/home.js"

import drawMixin from "@/utils/drawMixin"

import Header from "@/components/Header"
import BussinessOverview from "./components/BussinessOverview.vue"
import ProjectStatics from "./components/ProjectStatics.vue"
import RealtimeOverview from "./components/RealtimeOverview.vue"
import ReportStatics from "./components/ReportStatics.vue"
import Distribute from "./components/Distribute.vue"

export default {
  mixins: [drawMixin],
  data () {
    return {
      loading: true,
      bussinessOverviewData: {},
      realtimeOverviewData: {},
      projectStatisticsData: [],
      deptReportData: [],
      mapDistributeData: [],
      projectNum: 0,
      projectReportData: [],
      timing2: null,
      timing: null,
    }
  },
  components: {
    Header, BussinessOverview, ProjectStatics, RealtimeOverview, ReportStatics, Distribute
  },
  mounted () {
    this.cancelLoading()
    this.getAllData()
    this.getRealTime()
    this.timeFun()
    this.$once("hook:beforeDestroy", () => {
      clearInterval(this.timing)
      clearInterval(this.timing2)
      this.timing = null
      this.timing2 = null
    })
  },
  beforeDestroy () {
    clearInterval(this.timing)
  },
  methods: {
    cancelLoading () {
      setTimeout(() => {
        this.loading = false
      }, 500)
    },

    getAllData () {
      BussinessOverviewApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          this.bussinessOverviewData = data
        }
      })

      ProjectStatisticsApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          let count = 0
          let sum = 0
          data.forEach((item) => {
            if (item.projectState === '进行中') {
              this.projectNum = item.projectAmount
            }
            if (item.projectState.indexOf("已完工") > -1 || item.projectState == "办结") {
              count = count + item.projectAmount
            }
            sum = sum + item.projectAmount
          })

          this.projectStatisticsData = data

          this.projectStatisticsData.push({ projectState: "已完工", projectAmount: count })

          this.projectStatisticsData.forEach(item => {
            item.flag = 0 //判断高亮
            item.name = item.projectState
            item.value = item.projectAmount
            item.percentage = (item.projectAmount / sum * 100).toFixed(1) + '%' //百分比
            if (item.projectState == '未开始') {
              item.color = "#86D3FF"
            } else if (item.projectState == '进行中') {
              item.color = "#42B1C4"

            } else if (item.projectState == '作废') {
              item.color = "#AF8753"
            } else if (item.projectState == '终止') {
              item.color = "#D71F5E"
            } else if (item.projectState == '已完工') {
              item.color = "#1D91F7"
            }
            else if (item.projectState == '已结束') {
              item.color = "#A0A6AA"
            }
          })

          this.projectStatisticsData = this.projectStatisticsData.filter(item => item.projectState !== '已完工(报告交付完毕)' && item.projectState !== '办结')

        }
      })


      this.deptReportData = []
      ReportStatisticsApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          data.forEach(item => {
            this.deptReportData.push(item)
          })
        }
      })

      BranchProjectReportApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          data.forEach(item => {
            this.deptReportData.push(item)
          })
        }
      })


      // ProjectDistributeApi().then(res => {
      //   const { code, data } = res
      //   if (code == 0) {
      //     this.mapDistributeData = data
      //     console.log(data)

      //   }
      // })

      ProjectReportApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          this.projectReportData = data
        }
      })



    },
    timeFun () {
      this.timing = setInterval(() => {
        this.getRealTime()
      }, 6000)

      this.timing2 = setInterval(() => {
        this.getAllData()
      }, 60000)

    },

    getRealTime () {
      RealTimeOverviewApi().then(res => {
        const { code, data } = res
        if (code == 0) {
          this.realtimeOverviewData = data
        }
      })
    }

  }
}
</script>

