<template>
  <div id="project-statics">
    <img src="../../../assets/images/title_xmtj.png" />
    <Echart id="ProjectStaticsChart" ref="ProjectStaticsRef" :options="options" height="210px" width="280px" />

    <!-- 右侧指示框 -->
    <div class="right-legend">

      <div :class="item.flag ? 'legend-item active' : 'legend-item'" v-for="(item, index) in ApiData" :key="index" @mouseover="handleMouseOver(index)" @mouseleave="handleMouseLeave(index)" @click="handleClick(item.projectState)">

        <img v-if="item.flag" src="../../../assets/images/img_zuoshang.png" style="position: absolute;left:0;top:0">
        <img v-if="item.flag" src="../../../assets/images/img_youshang.png" style="position: absolute;right:0;top:0">
        <img v-if="item.flag" src="../../../assets/images/img_zuoxia.png" style="position: absolute;left:0;bottom:0">
        <img v-if="item.flag" src="../../../assets/images/img_youxia.png" style="position: absolute;right:0;bottom:0">

        <div class="legend-item-data">
          <div>
            <div class="legend-item-icon" :style="{ background: item.color }">
            </div>

            <span class="legend-item-percent">{{ item.percentage }}</span>
          </div>

          <div style="font-size: 14px;">
            {{ item.name }}
          </div>

        </div>

      </div>

    </div>

    <div style="padding: 10px 20px 0;height: 220px;">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <dv-scroll-board v-else :config="config" style="width: 420px;height:250px" :key="randomKey" />
    </div>

    <el-drawer title="详情" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="formLoading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;margin-top:24px">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="项目编号" prop="projectNumber" />
          <el-table-column label="合同编号" prop="contractNumber" />
          <el-table-column label="所属公司" prop="ownedCompany" />
          <el-table-column label="所属片区" prop="area" />
          <el-table-column label="原因" prop="entrustCode" v-if="handleType == '作废' || handleType == '终止'">
            <template>
              <span v-if="handle == '作废'">项目变更/ERP终止</span>
              <span v-if="handle == '终止'">ERP终止</span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange" @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { projectEntityPage } from "@/api/home.js"
import Echart from '@/common/echart'
import echartImg from "@/assets/images/icon_xmsl.png"

export default {
  props: {
    ApiData: {
      type: Array,
      require: true,
      default: () => { }
    },
    ProjectReportData: {
      type: Array,
      require: true,
      default: () => { }
    }
  },
  watch: {
    ProjectReportData: {
      handler(val) {
        if (val && val[0]) {

          this.changeData()
          this.initChart()//初始化
          this.timeFun()//开启定时器
          this.$nextTick(() => {
            //添加悬浮事件
            const myChart = this.$echarts.init(document.getElementById('ProjectStaticsChart'))
            myChart.on('mouseover', (params) => {
              if (params.componentType == 'series') {
                this.handleMouseOver(params.dataIndex)
                // 清除定时器,清除上一次悬浮
              }
            })
            myChart.on('mouseout', (params) => {
              //鼠标移出
              if (params.componentType == 'series') {
                // 开启定时器
                this.index = 0
                // this.timeFun()
              }
            })
          })
        }

        if (val && val[0]) {
          this.changeData()
          this.Loading = false
        }
      },
      immediate: true,
      deep: true,
    },
  },
  data() {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    }
    return {
      formLoading: false,
      options: {},
      config: {
        header: ['项目名称', '报告数量'],
        data: [],
        headerBGC: "rgba(9,47,111,0.6)",
        oddRowBGC: "#0A1A37",
        evenRowBGC: "#0D2145"
      },
      innerData: [],
      index: 0,
      randomKey: Math.random(),
      Loading: true,
      timing: null,
      query: Object.assign({}, this.initQuery),
      drawerVisible: false,
      tableValue: [],
      handleType: 0,
    }
  },
  components: {
    Echart,
  },
  mounted() {
    // this.$once("hook:beforeDestroy", () => {
    //   clearInterval(this.timing)
    //   this.timing = null
    // })
  },
  // beforeDestroy () {
  //   clearInterval(this.timing)
  //   this.timing = null
  // },
  methods: {
    handleClick(index) {
      this.handleType = index
      this.drawerVisible = true
      index == "已完工" ? this.query.projectState = "已完工(报告交付完毕)" : this.query.projectState = index;
      this.toQuery()
    },
    toQuery() {
      this.formLoading = true
      projectEntityPage(this.query).then(res => {
        if (res.code == 0) {
          this.formLoading = false
          this.tableValue = res.data.records
          this.query.total = res.data.total
        }
      }).finally(() => {
        this.formLoading = false
      })
    },
    paginationCurrentChange(pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex }
      this.toQuery()
    },
    handleSizeChange(pageSize) {
      this.query = { ...this.query, pageSize: pageSize }
      this.toQuery()
    },
    changeData() {
      this.config.data = this.ProjectReportData.map(item => {
        return [item.name, item.value]
      })
      this.randomKey = Math.random()

    },

    initChart() {
      this.options = {
        graphic: {//图形中间图片
          elements: [{
            type: "image",
            style: {
              image: echartImg,
              width: 41,
              height: 40,
            },
            left: "center",
            top: "30%",
          }],
        },
        grid: {
          top: "0%",
          left: "2%",
          right: "2%",
          bottom: "0%"
          // containLabel: true
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} :  {c}"
        },
        // silent: true,
        legend: {
          show: false,
          right: "80px",
          top: "15px",
          icon: "circle",
          orient: "vertical",
          align: "auto",
          itemWidth: 10,
          textStyle: {
            color: "#fff",
          },
          itemGap: 20,
          formatter: (name) => {
            let text = ""
            this.ApiData.forEach((val) => {
              if (val.name === name) {
                text = val.percentage + '\n' + name
              }
            })
            return text
          },
          data: this.ApiData.map((val) => val.name)
        },
        series: [
          {
            type: "pie",
            radius: ["82%", "92%"],
            center: ["50%", "50%"],
            color: ["#42B1C4", "#A0A6AA", "#AF8753", "#86D3FF", "#D71F5E", "#1D91F7"],
            itemStyle: {
              borderColor: "#031845",
              borderWidth: 5
            },
            data: this.ApiData,
            labelLine: {
              show: false
            },

            label: {
              // show: false,
              normal: {
                show: false,
                position: 'center',
                formatter: '\n\n\n\n{value|{c}}',
                rich: {
                  value: {
                    padding: 5,
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 32,
                  },
                  label: {
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 16,
                  },
                },
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                },
              },
            }
          },
          {
            type: 'pie',
            center: ["50%", "50%"],
            radius: ['55%', '75%'],
            color: ["#002E48", "#370F36"],
            silent: true,
            label: {
              show: false,
            },
            labelLine: {
              show: false,
              length: 0,
              length2: 0,
            },
            data: this.innerData
          }

        ]
      }
    },

    handleMouseOver(index) {
      clearInterval(this.timing)//悬浮时清除定时器
      this.clearAllTip()

      this.ApiData.forEach(item => item.flag = 0)
      this.ApiData[index].flag = 1
      var myChart = this.$echarts.init(document.getElementById('ProjectStaticsChart'))
      myChart.dispatchAction({
        type: 'showTip',
        seriesIndex: 0,
        dataIndex: index
      })
      myChart.dispatchAction({
        type: 'highlight',
        seriesIndex: 0,
        dataIndex: index
      })
      this.innerPie(index)
    },

    handleMouseLeave(index) {
      //悬浮离开，重置定时器
      this.index = 0
      // this.timeFun()

      this.ApiData[index].flag = 0
      var myChart = this.$echarts.init(document.getElementById('ProjectStaticsChart'))
      myChart.dispatchAction({
        type: 'downplay',
        seriesIndex: 0,
        dataIndex: index
      })
    },

    //内圈数量修改
    innerPie(index) {
      let count = 0
      this.ApiData.forEach((item, i) => {
        if (i !== index) {
          count = count + item.value
        }
      })
      this.innerData = [this.ApiData[index], { value: count, name: "其他" }]
      this.initChart()
    },

    timeFun() {
      setTimeout(() => {
        var myChart = this.$echarts.init(document.getElementById('ProjectStaticsChart'))
        this.ApiData.forEach((item) => {
          item.flag = 0
        })

        //先清除上一次的悬浮
        this.clearAllTip()

        this.ApiData[this.index].flag = 1
        myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: this.index
        })
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: this.index
        })
        this.innerPie(this.index)

        this.index = this.index == 5 ? 0 : this.index + 1
      }, 1000)
    },

    //清除所有悬浮提示
    clearAllTip() {
      var myChart = this.$echarts.init(document.getElementById('ProjectStaticsChart'))
      for (var i = 0; i < this.ApiData.length; i++) {
        myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
          dataIndex: i
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
#project-statics {
  width: 460px;
  height: 548px;
  // background: url("~@/assets/images/title_xmtj.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
  position: relative;

  .right-legend {
    position: absolute;
    top: 80px;
    right: 30px;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    grid-row-gap: 10px;
    grid-column-gap: 10px;

    .legend-item {
      width: 86px;
      height: 53px;
      color: #9dc7d7;
      position: relative;
      // text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      // line-height: 53px;
      &:hover {
        cursor: pointer;
      }

      .legend-item-data {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .legend-item-icon {
          width: 10px;
          height: 6px;
          border-radius: 1px;
          display: inline-block;
          margin-right: 5px;
        }

        .legend-item-percent {
          font-size: 18px;
        }
      }
    }

    .legend-item.active {
      background: #0d3969;
      opacity: 0.64;
    }
  }
}

::v-deep .header-item {
  font-weight: 550;
  color: #ecf8ff;

  &:first-child {
    width: 300px !important;
  }

  &:nth-child(2) {
    width: 120px !important;
  }
}

::v-deep .rows {
  color: #ecf8ff;
}

::v-deep .row-item {
  .ceil:first-child {
    width: 300px !important;
  }

  .ceil:nth-child(2) {
    width: 120px !important;
  }
}
</style>
