<template>
  <div id="report-statics">
    <img src="../../../assets/images/title_bgtj.png" />
    <div class="report-statics-body">
      <dv-loading v-if="Loading" style="">数据加载中...</dv-loading>
      <dv-scroll-ranking-board v-else :config="config" style="width: 420px; height: 550px;" :key="randomKey" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    ApiData: {
      type: Array,
      require: true,
      default: () => { }
    }
  },
  watch: {
    ApiData: {
      handler (val) {
        if (val && val[0]) {
          this.$nextTick(() => {
            this.config.data = val
            this.randomKey = Math.random() //刷新组件
            this.Loading = false
          })
        }
      },
      immediate: true,
      deep: true,
    },
  },
  data () {
    return {
      config: {
        data: [],
        rowNum: 10,
        waitTime: 3000,
      },
      randomKey: Math.random(),
      Loading: true,
    }
  }
}
</script>

<style lang="scss" scoped>
#report-statics {
  width: 460px;
  height: 620px;
  // background: url("~@/assets/images/title_bgtj.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .report-statics-body {
    height: 480px;
    padding: 10px 10px 10px 20px;
  }
}

::v-deep .rank {
  color: #E9E9E9 !important;
}

::v-deep .dv-scroll-ranking-board .row-item:nth-child(odd)>.ranking-column .inside-column {
  background: linear-gradient(270deg, rgba(15, 66, 115, 1), rgba(93, 194, 255, 1));
}

::v-deep .dv-scroll-ranking-board .row-item:nth-child(even)>.ranking-column .inside-column {
  background: linear-gradient(270deg, rgba(115, 75, 15, 1), rgba(255, 172, 93, 1));
}

::v-deep .dv-scroll-ranking-board .ranking-column {
  background-color: #002947;
  border-bottom: 0;
}
</style>
