<template>
  <div class="root">
    <div class="container-shell">
      <div id="container" ref="container" :style="styleValue">
        <!-- <button @click="fullscreen" style="position: absolute;bottom: 0;right: 0;z-index: 9999;">全屏</button> -->
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "DemoPlayer",
  props: {
    equNumber: {
      type: String,
    },
    styleValue: {
      type: String,
      default: () => "width:300px;height:200px;",
    },
  },
  data() {
    return {
      jessibuca: null,
      version: "",
      playing: false,
      quieting: true,
      loaded: false, // mute
      showOperateBtns: true,
      showBandwidth: false,
      err: "",
      performance: "",
      useWCS: false,
      useMSE: true,
      useOffscreen: false,
    };
  },
  watch: {
    equNumber: {
      handler(val) {
        if (val) {
          setTimeout(() => {
            this.play();
          }, 2000);
        }
        //  else {
        //   console.log(val)
        //   this.jessibuca.destroy()
        // }
      },
      immediate: true,
      deep: true,
    },
  },
  mounted() {
    window.onerror = (msg) => (this.err = msg);
    this.create();
  },
  destroyed() {
    this.jessibuca.destroy();
  },
  methods: {
    create(options) {
      options = options || {};
      this.jessibuca = new window.Jessibuca(
        Object.assign(
          {
            container: this.$refs.container,
            // videoBuffer: 0.5, // 缓存时长
            isResize: false,
            useWCS: this.useWCS,
            useMSE: this.useMSE,
            text: "",
            // autoWasm: true,
            // background: "bg.jpg",
            loadingText: "疯狂加载中...",
            // hasAudio:false,
            debug: true,
            supportDblclickFullscreen: true,
            showBandwidth: this.showBandwidth, // 显示网速
            operateBtns: {
              fullscreen: this.showOperateBtns,
              screenshot: false, // this.showOperateBtns,
              play: this.showOperateBtns,
              audio: false, //this.showOperateBtns,
            },
            vod: this.vod,
            forceNoOffscreen: !this.useOffscreen,
            isNotMute: true,
            timeout: 100,
          },
          options
        )
      );
      var _this = this;
      // this.jessibuca.on("load", function () {
      //   console.log("on load")
      // })

      // this.jessibuca.on("log", function (msg) {
      //   console.log("on log", msg)
      // })
      // this.jessibuca.on("record", function (msg) {
      //   console.log("on record:", msg)
      // })
      this.jessibuca.on("pause", function () {
        console.log("on pause");
        _this.playing = false;
      });
      this.jessibuca.on("play", function () {
        console.log("on play");
        _this.playing = true;
      });
      this.jessibuca.on("fullscreen", function (msg) {
        console.log("on fullscreen", msg);
      });

      this.jessibuca.on("mute", function (msg) {
        console.log("on mute", msg);
        _this.quieting = msg;
      });

      // this.jessibuca.on("mute", function (msg) {
      //   console.log("on mute2", msg)
      // })

      // this.jessibuca.on("audioInfo", function (msg) {
      //   console.log("audioInfo", msg)
      // })

      // this.jessibuca.on("videoInfo", function (info) {
      //   console.log("videoInfo", info)
      // })

      // this.jessibuca.on("error", function (error) {
      //   console.log("error", error)
      // })

      // this.jessibuca.on("timeout", function () {
      //   console.log("timeout")
      // })

      // this.jessibuca.on('start', function () {
      //   console.log('frame start')
      // })

      // this.jessibuca.on("performance", function (performance) {
      //   var show = "卡顿"
      //   if (performance === 2) {
      //     show = "非常流畅"
      //   } else if (performance === 1) {
      //     show = "流畅"
      //   }
      //   _this.performance = show
      // })
      // this.jessibuca.on('buffer', function (buffer) {
      //   console.log('buffer', buffer)
      // })

      // this.jessibuca.on('stats', function (stats) {
      //   console.log('stats', stats)
      // })

      // this.jessibuca.on('kBps', function (kBps) {
      //   console.log('kBps', kBps)
      // })

      // this.jessibuca.on("play", () => {
      //   this.playing = true
      //   this.loaded = true
      //   this.quieting = this.jessibuca.isMute()
      // })

      // this.jessibuca.on('recordingTimestamp', (ts) => {
      //   console.log('recordingTimestamp', ts)
      // })
    },
    async play() {
      // 从接口获取地址播放
      if (this.equNumber) {
        const res = await axios.get(
          `http://113.247.236.85:18080/api/play/start/` + this.equNumber
        );
        const { code, data } = res.data;
        if (code == 0) {
          console.log(data);
          this.jessibuca.play(data.flv);
        }
      }
    },

    destroy() {
      if (this.jessibuca) {
        this.jessibuca.destroy();
      }
      this.create();
      this.playing = false;
      this.loaded = false;
      this.performance = "";
    },

    // fullscreen () {
    //   this.jessibuca.setFullscreen(true)
    // },
  },
};
</script>
<style>
.root {
  display: flex;
  place-content: center;
  /* margin-top: 3rem; */
}

.container-shell {
  position: relative;
  backdrop-filter: blur(5px);
  background: hsla(0, 0%, 50%, 0.5);
  padding: 20px 4px 10px 4px;
  /* border: 2px solid black; */
  width: auto;
  position: relative;
  border-radius: 5px;
  box-shadow: 0 10px 20px;
}

.container-shell-title {
  position: absolute;
  color: darkgray;
  top: 4px;
  left: 10px;
  text-shadow: 1px 1px black;
}

#container {
  background: rgba(13, 14, 27, 0.7);
  position: relative;
}

.input {
  display: flex;
  align-items: center;
  margin-top: 10px;
  color: white;
  place-content: stretch;
}

.input2 {
  bottom: 0px;
}

.input input[type="input"] {
  flex: auto;
}

.err {
  position: absolute;
  top: 40px;
  left: 10px;
  color: red;
}

.option {
  position: absolute;
  top: 4px;
  right: 10px;
  display: flex;
  place-content: center;
  font-size: 12px;
}

.option span {
  color: white;
}

.page {
  background: url(/bg.jpg);
  background-repeat: no-repeat;
  background-position: top;
}

@media (max-width: 720px) {
  #container {
    width: 90vw;
    height: 52.7vw;
  }
}
</style>
