<template>
  <div id="bussiness-overview">
    <img src="../../../assets/images/title_ywzl.png">
    <div class="bussiness-info">
      <div class="bussiness-item" v-for="(item, index) in bussinessData" :key="index" @mouseover="handleMouseOver(index)" @mouseleave="handleMouseLeave(index)" @click="handleClick(index)">
        <img :src="!item.flag ? item.img : item.img_hover" class="bussiness-img">
        <div class="bussiness-data">
          <span>{{ item.name }}</span>
          <div style="display: flex;">
            <span class="bussiness-num">
              <CountTo :startVal='startVal' :endVal='item.count' :duration='duration' separator="," />
            </span>{{ item.unit }}
          </div>
        </div>
      </div>
    </div>
    <el-drawer :title="index == 0?'已出具报告':(index==1?'待出具报告':'详情')" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 0 || index == 1">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber" />
          <el-table-column label="报告状态" prop="reportStatus" width="80" />
          <el-table-column label="检测指标" prop="parameterName" />
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="委托时间" prop="entrustDate" width="110" />
          <el-table-column label="收样时间" prop="sampleReceiveTime" width="110" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 2">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="仪器编号" prop="equipmentNumber" />
          <el-table-column label="仪器名称" prop="equipmentName" />
          <el-table-column label="仪器类型" prop="equipmentType" />
          <el-table-column label="归属部门" prop="deptName" />
          <el-table-column label="存放地点" prop="storagePlace" />
          <el-table-column label="检定日期" prop="checkTime" />
          <el-table-column label="检定有效期" prop="checkValidityTime" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 3">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="参数名称" prop="parName" />
          <el-table-column label="状态" prop="isUsed">
            <template slot-scope="scope">
            {{ scope.row.isUsed==1?"可用":"不可用" }}
          </template>
          </el-table-column>
          <el-table-column label="计费单位" prop="unitOfCharging" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 4">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="项目编号" prop="projectNumber" />
          <el-table-column label="合同编号" prop="contractNumber" />
          <el-table-column label="所属公司" prop="ownedCompany" width="100"/>
          <el-table-column label="所属片区" prop="area" width="100"/>
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 5">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="用户名称" prop="userName" width="100"/>
          <el-table-column label="员工编号" prop="userNumber" width="100"/>
          <el-table-column label="性别" prop="userSex" width="80">
            <template slot-scope="scope">
            {{ scope.row.userSex?"男":"女" }}
          </template>
          </el-table-column>
          <el-table-column label="所属部门" prop="deptNames" show-overflow-tooltip />
          <el-table-column label="手机号" prop="userPhone" />
          <el-table-column label="岗位" prop="positions" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange" @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { ReportListApi, equipmentEntityPage, parParameterEntityPage, projectEntityPage, getUserPage } from "@/api/home.js"
import CountTo from "vue-count-to"
import ycjbg from "../../../assets/images/icon_jinrichujubaogao.png"
import ycjbg_hover from "../../../assets/images/icon_yichujubaogao_1.png"

import dcjbg from "../../../assets/images/icon_daichujubaogao.png"
import dcjbg_hover from "../../../assets/images/icon_daichujubaogao_1.png"

import sbzl from "../../../assets/images/icon_shenbeizongliang.png"
import sbzl_hover from "../../../assets/images/icon_shebeizongliang_1.png"

import zzcs from "../../../assets/images/icon_zizhicanshu.png"
import zzcs_hover from "../../../assets/images/icon_zizhicanshu_1.png"

import xmzs from "../../../assets/images/icon_xaingmuzongshu.png"
import xmzs_hover from "../../../assets/images/icon_xiangmuzongshu_1.png"

import jcry from "../../../assets/images/icon_jiancerenyuan.png"
import jcry_hover from "../../../assets/images/icon_jiancerenyuan_1_1.png"

export default {
  props: {
    ApiData: {
      type: Object,
      require: true,
      default: () => { }
    }
  },
  components: {
    CountTo
  },
  data() {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    }
    return {
      Loading: false,
      drawerVisible: false,
      startVal: 0,
      duration: 1000,
      bussinessData: [
        { name: "已出具报告", count: 0, img: ycjbg, unit: "份", img_hover: ycjbg_hover, flag: 0 },
        { name: "待出具报告", count: 0, img: dcjbg, unit: "份", img_hover: dcjbg_hover, flag: 0 },
        { name: "设备总量", count: 0, img: sbzl, unit: "个", img_hover: sbzl_hover, flag: 0 },
        { name: "资质参数", count: 0, img: zzcs, unit: "个", img_hover: zzcs_hover, flag: 0 },
        { name: "项目总数", count: 0, img: xmzs, unit: "个", img_hover: xmzs_hover, flag: 0 },
        { name: "检测人员", count: 0, img: jcry, unit: "人", img_hover: jcry_hover, flag: 0 },
      ],
      tableValue: [],
      query: Object.assign({}, this.initQuery),
      index: 0,
    }
  },
  watch: {
    ApiData: {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            this.bussinessData[0].count = this.ApiData.outReportAmount
            this.bussinessData[1].count = this.ApiData.notOutReportAmount
            this.bussinessData[2].count = this.ApiData.equipmentAmount
            this.bussinessData[3].count = this.ApiData.parametersAmount
            this.bussinessData[4].count = this.ApiData.projectAmount
            this.bussinessData[5].count = this.ApiData.testerAmount
          })
        }
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    handleMouseOver(index) {
      this.bussinessData[index].flag = 1
    },
    handleMouseLeave(index) {
      this.bussinessData[index].flag = 0
    },
    handleClick(index) {
      this.index = index
      this.drawerVisible = true;
      this.query=Object.assign({}, this.initQuery);
      this.toQuery()

    },
    toQuery() {
      this.Loading = true
      if (this.index == 0 || this.index == 1) {
        this.index == 0 ? this.query.queryType = '已出具报告' : this.query.queryType = '待出具报告';
        ReportListApi(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      } else if (this.index == 2) {
        this.query.queryType="";
        equipmentEntityPage(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      } else if (this.index == 3) {
        this.query.queryType="";
        parParameterEntityPage(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      } else if (this.index == 4) {
        this.query.queryType="";
        projectEntityPage(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      } else if (this.index == 5) {
        this.query.queryType="";
        getUserPage(this.query).then(res => {
          if (res.code == 0) {
            this.Loading = false
            this.tableValue = res.data.records
            this.query.total = res.data.total
          }
        }).finally(() => {
          this.Loading = false
        })
      }

    },
    paginationCurrentChange(pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex }
      this.toQuery()
    },
    handleSizeChange(pageSize) {
      this.query = { ...this.query, pageSize: pageSize }
      this.toQuery()
    },
  }

}
</script>

<style lang="scss" scoped>
#bussiness-overview {
  width: 460px;
  height: 362px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .bussiness-info {
    padding: 18px 15px 0 15px;

    display: grid;
    grid-template-columns: repeat(2, 50%);
    // grid-template-rows: repeat(3, 33.33%);
    grid-row-gap: 36px;
    grid-column-gap: 15px;

    .bussiness-item {
      color: #dadfe4;
      display: flex;
      font-size: 14px;
      // background: url("~@/assets/images/icon_jiancerenyuan.png") no-repeat;

      &:hover {
        cursor: pointer;
      }

      .bussiness-img {
        width: 88px;
        height: 60px;
        margin-right: 15px;
      }

      .bussiness-data {
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .bussiness-num {
          color: #96ceff;
          font-size: 24px;
          margin-right: 5px;
        }
      }
    }
  }
}
</style>
